.App {
  text-align: center;
}

.App-header {
  position: relative;
  overflow: hidden;
}
.video-container {
  position: absolute;
  width: 100vw;
  height: 100vh; /* Set the height to cover the entire viewport */
}

.videoTag {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; /* Set the height to cover the entire viewport */
  object-fit: cover; /* Maintain aspect ratio and cover the entire container */
}

.text-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* Add other styles for text overlay */
}
.profileimage {
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}


.container{
  display: inline-block;
  font-family: "Agency FB";
  font-size: 30px;
}
.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative;
  overflow: hidden;
  border: 2px solid white;
  
}

.App-link {
  color: #61dafb;
}


a {
  text-decoration: none;
  display: inline-block;
  margin: 5px;
  color: #333;
  transition: color 0.3s;
}

a:hover {
  color: #007BFF;
}/* CSS for responsive image */
.responsive-image {
  max-width: 40%; /* Set the maximum width to 100% of its container */
  height: auto; /* Allow the height to adjust proportionally */
}
.right-aligned-container {
  left:50%; /* Align items to the right end */
}
